'use strict';
//{
//   code:0,
//   response:{
// 	   lat:'',维度
//     lon:'' 经度
//   }
// }
import {WinJSDec, appError, fnError} from './APP/config';

var name = 'LOCATION';
var LOCATION = (fn=_=>{}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('tools', 'getLocation', function (res) {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default LOCATION;